import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC } from 'react';
import MenuSection from '../MenuSection/MenuSection.lazy';
import './Tools.scss';
import { faStackOverflow,faGithub, faNpm, faLinkedin, faHackerrank } from '@fortawesome/free-brands-svg-icons'

interface ToolsProps {}

const Tools: FC<ToolsProps> = () => {  
  
  const redirectToLink=(url:string):any =>{
    const win = window.open(url, '_blank');
    if (win != null) {
      win.focus();
    }
    return win;
  }

  const getTools = (): JSX.Element[] => {
    return [{
      icon: faGithub,
      id: 1,
      image: "/assets/brand-logo/GitHub-Logo.svg",
      label: "Repo",
      link:"https://github.com/omkarpattanaik",
      name: "Github"
    }, {
      icon: faNpm,
      id: 2,
      image: "/assets/brand-logo/Npm-logo.svg",
      label: "Registry",
      link:"https://www.npmjs.com/~omkarpattanaik",
      name: "NPM"
    }, {
      icon: faStackOverflow,
      id: 3,
      image: "/assets/brand-logo/StackOverflow-Logo.svg",
      label: "Q&A",
      link:"https://stackoverflow.com/users/13779639/omkar-pattanaik?tab=profile",
      name: "StackOverflow"
    }, {
      icon: faLinkedin,
      id: 4,
      image: "/assets/brand-logo/LinkedIn-Logo.svg",
      label: "Social media",
      link:"https://www.linkedin.com/in/omkar-pattanaik-3b9b2512a/",
      name: "LinkedIn"
    }, {
      icon: faHackerrank,
      id: 5,
      image: "/assets/brand-logo/Hackerrank-Logo.png",
      label: "Certificates",
      link:"https://www.hackerrank.com/profile/omkarpattanaik",
      name: "HackerRank"
    }].map((tool: any) => {
      const styles: React.CSSProperties = {
        backgroundImage: `url(${tool.image})`,
        backgroundColor: "white",
        backgroundSize: 'contain'  
      }

      // const handleChange = (e) => {
      //   const win = window.open(url, '_blank');
      //   if (win != null) {
      //     win.focus();
      //   }
      // };
      
      return (
        <div key={tool.id} className="tool-card" onClick={(event: React.MouseEvent<HTMLElement>) => {
            redirectToLink(tool.link)
          }} >
          <div className="tool-card-background background-image" style={styles} />
          <div className="tool-card-content"  >            
            <div className="tool-card-content-header">            
              <span className="tool-card-label">{tool.label}</span>
              <span className="tool-card-name">{tool.name}</span>
            </div>
            <FontAwesomeIcon size="2x" icon={tool.icon} color="white"  />
          </div>
        </div>
      );
    })
  }
  
  return (
    <MenuSection icon="toolbox" id="tools-section" title="Find me on">
      {getTools()}
    </MenuSection>
  );

}
export default Tools;

